'use client'

import React, { useState, useEffect, useRef } from 'react'
import { Line } from 'react-chartjs-2'
import { Chart as ChartJS, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend } from 'chart.js'
import { Range } from 'react-range'
import axios from 'axios'

// 注册ChartJS插件
ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend)

export default function BtcPriceChart() {
  // 定义状态
  const [allData, setAllData] = useState([])
  const [displayData, setDisplayData] = useState([])
  const [progress, setProgress] = useState(0)
  const animationRef = useRef(null)

  // 获取数据
  useEffect(() => {
    const fetchData = async () => {
      const response = await axios.get('https://api.coincap.io/v2/assets/bitcoin/history', {
        params: { interval: 'd1', start: Date.now() - 2 * 365 * 24 * 60 * 60 * 1000, end: Date.now() }
      })
      const prices = response.data.data.map(item => ({
        date: new Date(item.time).toISOString().split('T')[0],
        price: parseFloat(item.priceUsd)
      }))
      setAllData(prices)
      setDisplayData(prices.slice(0, Math.floor(prices.length * 0.1)))
    }

    fetchData()
  }, [])

  // 动画效果
  useEffect(() => {
    const startTime = Date.now()
    const duration = 300000

    const animate = () => {
      const elapsedTime = Date.now() - startTime
      const newProgress = Math.min(elapsedTime / duration, 1)

      setProgress(newProgress)

      if (newProgress < 1) {
        animationRef.current = requestAnimationFrame(animate)
      } else {
        cancelAnimationFrame(animationRef.current)
      }
    }

    animationRef.current = requestAnimationFrame(animate)

    return () => cancelAnimationFrame(animationRef.current)
  }, [allData])

  // 根据进度更新显示数据
  useEffect(() => {
    const halfYearIndex = Math.floor(allData.length * 0.5)
    const startIndex = Math.max(0, halfYearIndex - Math.floor(allData.length * progress))
    const maxIndex = Math.min(startIndex + Math.floor(allData.length * 0.1), allData.length - 1)
    setDisplayData(allData.slice(startIndex, maxIndex + 1))
  }, [progress, allData])

  // 主图表配置
  const mainChartOptions = {
    responsive: true,
    plugins: {
      legend: { position: 'top' },
      title: { display: true, text: '比特币历史价格走势', color: '#333', font: { size: 18, weight: 'bold' } },
      tooltip: {
        mode: 'index',
        intersect: false,
        backgroundColor: 'rgba(0, 0, 0, 0.7)',
        titleColor: '#fff',
        bodyColor: '#fff',
        borderColor: '#fff',
        borderWidth: 1,
      },
    },
    scales: {
      x: {
        grid: { display: false },
        ticks: { color: '#666', font: { size: 10 } },
      },
      y: {
        grid: { color: 'rgba(0, 0, 0, 0.1)' },
        ticks: { color: '#666', font: { size: 10 } },
      },
    },
    interaction: {
      mode: 'nearest',
      axis: 'x',
      intersect: false,
    },
  }

  // 缩略图表配置
  const thumbnailChartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: { legend: { display: false }, tooltip: { enabled: false } },
    scales: { x: { display: false }, y: { display: false } },
  }

  // 获取最新价格
  const latestPrice = displayData.length > 0 ? displayData[displayData.length - 1].price : null

  return (
    <div className="max-w-4xl mx-auto p-4 bg-white rounded-lg shadow-lg">
      <div className="relative">
        <Line
          data={{
            labels: displayData.map(d => d.date),
            datasets: [{
              label: '比特币价格 (USD)',
              data: displayData.map(d => d.price),
              borderColor: 'rgb(75, 192, 192)',
              backgroundColor: 'rgba(75, 192, 192, 0.2)',
              tension: 0.4,
              pointHoverRadius: 5,
              pointHitRadius: 30,
              pointBackgroundColor: 'rgb(75, 192, 192)',
              pointBorderWidth: 2,
              pointBorderColor: '#fff',
              pointStyle: displayData.map((_, index) => index === displayData.length - 1 ? 'circle' : 'line'),
              pointRadius: displayData.map((_, index) => index === displayData.length - 1 ? 5 : 0),
            }]
          }}
          options={mainChartOptions}
        />
      </div>
      {latestPrice && (
          <div 
            className="absolute right-0 top-1/2 transform -translate-y-1/2 bg-white px-2 py-1 rounded-md shadow-md"
            style={{ marginRight: '10px' }}
          >
            ${latestPrice.toFixed(2)}
          </div>
        )}
      <div className="mt-2 relative" >
        <Line
          data={{
            labels: allData.map(d => d.date),
            datasets: [{
              data: allData.map(d => d.price),
              borderColor: 'rgb(75, 192, 192)',
              borderWidth: 1,
              pointRadius: 0,
              fill: true,
              backgroundColor: 'rgba(75, 192, 192, 0.2)',
            }]
          }}
          options={thumbnailChartOptions}
        />
        <Range
          step={0.001}
          min={0}
          max={1}
          values={[progress]}
          onChange={([newProgress]) => setProgress(newProgress)}
          renderTrack={({ props, children }) => (
            <div
              {...props}
              style={{
                ...props.style,
                height: '50%', // 修改高度为50%
                width: '100%',
                position: 'absolute',
                top: 0,
                backgroundColor: 'rgba(0,0,0,0.1)',
                borderRadius: '4px',
              }}
            >
              <div
                style={{
                  position: 'absolute',
                  height: '50%', // 修改高度为50%
                  width: `${progress * 100}%`,
                  backgroundColor: 'rgba(75, 192, 192, 0.5)',
                  borderRadius: '4px',
                }}
              />
              {children}
            </div>
          )}
          renderThumb={({ props }) => (
            <div
              {...props}
              style={{
                ...props.style,
                height: '50%', // 修改高度为50%
                width: '8px',
                backgroundColor: 'rgb(75, 192, 192)',
                borderRadius: '4px',
                boxShadow: '0 2px 6px rgba(0,0,0,0.3)',
              }}
            />
          )}
        />
      </div>
    </div>
  )
}